body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(213, 218, 231, 0.671);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Container {
  display: flex;
  margin: 10px;
  padding: 5px;
}

.Main-header {
  text-align: center;
  font-size: 44px;
  text-underline-position: below;
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  text-shadow: 2px 2px 10px;
}

@media screen and (max-width : 401px) {
  .Container {
    flex-direction: column;
  }
}
.Editor {
  display: flex;
  flex: 1 1;  
  flex-direction: column;
  border: 1px solid black;
  border-radius: 5px;
}

.Textarea {
  width: 99%;
  overflow: scroll;
  height: 80vh;
  align-self: center;
  font-size: 20px;
  background-color: rgba(238, 238, 238, 0.863);
  outline: none;
  padding: 5px;
}

.Header {
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  background-color: rgba(251, 47, 47, 0.493);
  padding: 5px;
  border-bottom: 1px solid black;
}
.Previewer {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  margin-left: 5px;
  border: 1px solid black;
  border-radius: 5px;
}

div {
  width: 99%;
}

.Previewer div:nth-child(2) {
  width: 98.7%;
  height: 80vh;
  padding: 5px;
  overflow: scroll;
  flex-wrap: wrap;
}
#preview {
  background-color: #aaeedd;
}
